<template>
	<div class="login">
		<p class="title">标题</p>
		<div class="content">
			<div class="input_container">
				<input class="uname"
					placeholder="用户名" 
					v-model="uname" 
					@input="limitInputuName"/>	
			</div>
			<div class="input_container">
				<input class="pwd"
					placeholder="密码" 
					v-model="pwd"
					@input="limitInputPwd"/>		
			</div>
		</div>
	
		<div class="btn_login" v-on:click="login()">登录</div>

	</div>
</template>

<script>
import md5 from 'js-md5';

	export default {
		name: 'Login',
		props: {
			msg: String
		},
		data() {
			return {
				user: {},
				num: 261777224,
				timeStamp: 0,
				signature: '',
				uname: '',
				pwd: ''
			}
		},
		methods: {
			limitInputuName(e){
				this.uname=e.target.value.replace(/[^\w\.\/]/ig,'');
			},
			limitInputPwd(e){
				// this.pwd=e.target.value.replace(/[^\w\.\/]/ig,'');
			},
			getCurTime() {
				return new Date().getTime()
			},
			getSignature() {
				var time = this.timeStamp + ''
				var number = 261777224 / 2 + ''
				return md5((time).split('').reverse().join('') + (number).split('').reverse().join(''))
			},
			login() {
				this.timeStamp = this.getCurTime()
				this.signature = this.getSignature()

				this.$axios({
						url: '/',
						method: 'post',
						data: {
							do: 'login',
							username: this.uname,
							password: md5(this.pwd)
						},
						transformRequest: [function(data) {
							let ret = ''
							for (let it in data) {
								ret += encodeURIComponent(it) + '=' + encodeURIComponent(data[it]) + '&'
							}
							return ret
						}],
						headers: {
							'num': this.num,
							'timeStamp': this.timeStamp,
							'signature': this.signature
						}
					})
					.then(this.loginSuccess)
			},
			loginSuccess(res) {
				res = res.data
				if (res.code == 200 && res.data) {
					const data = res.data
					this.user = data.list
					
					console.log(JSON.stringify(this.user))

					this.$router.push({
						name: 'Home',
						params: { /* 路由传参 */
							user: this.user
						}
					})

				}
			}
		}
	}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="stylus" scoped>
	.login
		margin .2rem
		width 100%
		height 100%
		.title
			text-align center
			padding .4rem
			font-size 0.6rem
		.content
			text-align center
			margin-top 1rem
			.input_container
				position relative
				margin auto
				border-bottom: .02rem solid gray
				margin-top .2rem
				width 90%
				font-size 0.4rem
				.uname
					width 100%
				.pwd
					width 100%
		.btn_login
			margin auto
			text-align center
			margin-top .4rem
			padding .2rem
			background-color #008CBA
			border-radius .1rem
			color white
			width 90%
</style>
