<template>
	<div class="home">
		<p class="title">首页</p>
		<div class="list" v-for="(done,index) in haveDone" :key="index">
			{{index}}--{{done.MFL_ID}}--{{done.MWF_Name}}
		</div>
		<div class="btn" v-on:click="vue2android()">vue调用android</div>
	</div>
</template>

<script>
import md5 from 'js-md5';
	
		export default {
			name: 'Home',
			data(){
				return{
					user:{},
					num: 261777224,
					timeStamp: 0,
					signature: '',
					haveDone: []
				}
			},
			mounted: function(){
				this.user= this.$route.params.user
				this.getHaveDone()
				this.android2vue()
			},
			methods: {
				getCurTime() {
					return new Date().getTime()
				},
				getSignature() {
					var time = this.timeStamp + ''
					var number = 261777224 / 2 + ''
					return md5((time).split('').reverse().join('') + (number).split('').reverse().join(''))
				},
				getHaveDone(){
					this.timeStamp = this.getCurTime()
					this.signature = this.getSignature()
					
					this.$axios({
					  url: '/',
					  method: 'post',
					  data: {
					    do: 'haveDone',
					    SFU_ID: this.user.SFU_ID,
					    token: this.user.token,
							pagesize:5,
					  },
					  transformRequest: [function (data) {
					    let ret = ''
					    for (let it in data) {
					      ret += encodeURIComponent(it) + '=' + encodeURIComponent(data[it]) + '&'
					    }
					    return ret
					  }],
					  headers: {
							'num': this.num,
							'timeStamp': this.timeStamp,
							'signature': this.signature
					  }
					})
					.then(this.getHaveDoneSuccess)
				},
				getHaveDoneSuccess(res){
					res = res.data
					if (res.code == 200 && res.data) {
						const data = res.data
						
						this.haveDone=[]
						this.haveDone=data.list
					}
				},
				vue2android(){
					 let msg = this.msg
					    this.$bridge.callHandler('dataToAndroid',msg,(res)=>{
					      alert('获取app响应数据:'+res)
					    })
				},
				android2vue(){
					  this.$bridge.registerHandler('dataToJs', (data, responseCallback) => {
					      alert('app主动调用js方法，传入数据:'+ data)
					      responseCallback(data)
					    })
				}
			}
		}
</script>

<style  lang="stylus" scoped>
	.home
		margin .2rem
		.title
			text-align center
			padding .4rem
			font-size 0.6rem
		.list
			padding .2rem
			font-size 0.4rem
		.btn
			padding .2rem
			margin .2rem
			background-color #008CBA
			border-radius .1rem
			color white
</style>
